import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { IPersona } from '../persona';
import { PersonasService } from '../personas.service';
import { Router,ActivatedRoute } from '@angular/router';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-personas-form',
  templateUrl: './personas-form.component.html',
  styleUrls: ['./personas-form.component.css']
})
export class PersonasFormComponent implements OnInit {


    constructor(private fb: FormBuilder, private personasServices: PersonasService, private router: Router, private router2 = ActivatedRoute) {
    }


    
    modoEdicion: boolean = false;
    formGroup: FormGroup;
    personaID: number;
    ngOnInit() {
        this.formGroup = this.fb.group({
            nombre: '',
            fechaNacimiento: ''
        });
        this.router2.prototype.params.subscribe(params => {
            if (params["id"] == undefined) {
                return;
            }
            this.modoEdicion = true;
            this.personaID = params["id"];
            this.personasServices.getPersona(this.personaID.toString()).subscribe(persona => this.cargarFormulario(persona),
                error => console.error(error));
        }); 
    }


    save() {
        let persona: IPersona = Object.assign({}, this.formGroup.value);
        console.table(persona);
        this.personasServices.createPersona(persona).subscribe(person => this.onSaveSF(),
            error => console.error(error));
    }
    cargarFormulario(person: IPersona) {
        var dp = new DatePipe(navigator.language);
        var format = "yyyy-MM-dd";
        this.formGroup.patchValue(
            {
                nombre: person.nombre,
                fechaNacimiento: dp.transform(person.fechaNacimiento, format)
            });
    }
    onSaveSF() {
        this.router.navigate(["/personas"]);
    }

}
